html, body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: #222831;
  }
  .hero-wrapper {
    position: fixed;
    bottom: 0;
    right: 0;
    height: auto;
    width: auto;
    min-height: 100%;
    min-width: 100%;
    z-index: -9999; 
  }
  
  .video {
    position: absolute;
  left: 50%; /* relative to nearest positioned ancestor or body element */
  top: 50%; /*  relative to nearest positioned ancestor or body element */
  -webkit-transform: translate(-50%, -32%);
      -ms-transform: translate(-50%, -32%);
          transform: translate(-50%, -32%); /* relative to element's height & width */
    width: 119%;
    height: 150vw;
    display:block; }
    
    .play {
      display:inline-block;
      position:-webkit-sticky;
      position:sticky;
      left: 20%; bottom: 1%;
         line-height: 80%; padding: 2%; border-radius: 2%px; 
         font-family: 'Domine', 'Helvetica'; font-weight: 700; 
         text-decoration: none; font-style: normal; 
         font-variant: small-caps; text-transform: capitalize; }
        .play:hover {
        background: #696969; }
        .play:active {
        background: #5e5e5e; }

  .pdf {
    display:inline-block;
    position:-webkit-sticky;
    position:sticky;
    left: 50%; 
    bottom: 46%;
     line-height: 80%; padding: 2%; border-radius: 2%px; 
     font-family: 'Domine', 'Helvetica'; font-weight: 700; 
     text-decoration: none; font-style: normal; 
     font-variant: small-caps; text-transform: capitalize; }
    .pdf:hover {
    background: #696969; }
    .pdf:active {
    background: #5e5e5e; }



figure {
  margin: 0;
  position: relative;
}
figure h1 {
  position: absolute;
  top: 2%;
  left: 2%;
  border-right: 3px solid #fff;
  padding: 5px 10px 5px 5px;
  font-size: 150%;
}
figcaption{
  color: #fff;
  position:relative;
  top: 1vw;
 
}


.stretch {
  height: 2000%;
  background:rgb(59, 59, 68) ;
  min-height: 100vh;
  min-width: 100vw;
  background-size: cover;
  margin-top: 60vw;

}
.stretch h1 {
  padding: 19% 2% 0 0;
  color: rgb(255, 255, 255);
    padding-bottom: 100%;
  font-size: 150%;

}
